import { NextRequest, NextResponse } from 'next/server';
import { cookies } from 'next/headers';
import UserAuthService from '@services/user.auth.service';
import type { User } from '@interfaces/user.interface';
import type { AuthResponse } from '@interfaces/auth.interface';

const userAuthService = new UserAuthService();

interface LoginRequest {
  email: string;
  password: string;
}

// Next.js 16 best practice: Use async request APIs
export async function POST(request: NextRequest): Promise<NextResponse<AuthResponse>> {
  try {
    // Next.js 16: cookies() and headers() are now async
    const cookieStore = await cookies();
    
    const body: LoginRequest = await request.json();
    
    if (!body.password) {
      return NextResponse.json<AuthResponse>(
        {
          error: true,
          message: 'Password is required',
        },
        { 
          status: 400,
          headers: {
            'Cache-Control': 'no-store',
          },
        }
      );
    }

    // Use Supabase Auth for login
    const { user, session } = await userAuthService.login(body);
    
    if (!session) {
      return NextResponse.json<AuthResponse>(
        {
          error: true,
          message: 'Failed to create session',
        },
        { 
          status: 500,
          headers: {
            'Cache-Control': 'no-store',
          },
        }
      );
    }

    // Set secure cookies for Supabase Auth session
    // Supabase uses sb-<project-ref>-auth-token for the session cookie
    if (session.access_token) {
      cookieStore.set('sb-access-token', session.access_token, {
        httpOnly: true,
        secure: process.env.NODE_ENV === 'production',
        sameSite: 'lax',
        maxAge: session.expires_in || 60 * 60 * 24 * 7, // Use session expiry or 7 days
        path: '/',
      });
    }

    if (session.refresh_token) {
      cookieStore.set('sb-refresh-token', session.refresh_token, {
        httpOnly: true,
        secure: process.env.NODE_ENV === 'production',
        sameSite: 'lax',
        maxAge: 60 * 60 * 24 * 30, // 30 days for refresh token
        path: '/',
      });
    }

    // Also set auth-token for backward compatibility
    cookieStore.set('auth-token', JSON.stringify({ user, token: session.access_token }), {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      sameSite: 'lax',
      maxAge: session.expires_in || 60 * 60 * 24 * 7,
      path: '/',
    });
    
    return NextResponse.json<AuthResponse>({
      error: false,
      message: 'Login successful',
      data: {
        ...user,
        token: session.access_token,
      } as User & { token?: string },
    }, {
      headers: {
        'Cache-Control': 'no-store',
      },
    });
  } catch (error: unknown) {
    const httpError = error as { status?: number; message?: string };
    return NextResponse.json<AuthResponse>(
      {
        error: true,
        message: httpError.message || 'Login failed',
      },
      { 
        status: httpError.status || 500,
        headers: {
          'Cache-Control': 'no-store',
        },
      }
    );
  }
}
